import { ErrorMessage, TopicType } from './constants';

const helpers = context => {
  const i18n = context.$filter('i18n');

  const headers = [{
    label: 'header.sender',
  }, {
    label: 'header.subject',
  }, {
    label: 'header.topic',
  }, {
    label: 'header.favorite',
    hidden: true,
  }, {
    label: 'header.date',
  }, {
    label: 'header.actions',
    hidden: true,
  }];

  /**
   * @name Helpers#getTopicType
   * @type {function}
   *
   * @description
   * Helper to get topic type
   *
   * @param {string} topic Topic of conversation
   * @returns {string} Type of topic
   */
  const getTopicType = (topic) => TopicType[topic] || TopicType.default;

  /**
   * @name Helpers#getTopicLabel
   * @type {function}
   *
   * @description
   * Helper to get topic label
   *
   * @param {string} topic List of topics
   * @param {string} topic Topic of conversation
   * @returns {string} Topic label
   */
  const getTopicLabel = (topics = [], topic) =>
    (topics.find(item => item.code === topic) || {}).name || topic;
  /**
   * @name Helpers#isSelected
   *
   * @description
   * Helper to know if the conversation is selected.
   *
   * @param {Object} conversation
   * @param {Array} selected
   * @type {function}
   * @returns {boolean}
   */
  const isSelected = (conversation, selected) => selected.indexOf(conversation) !== -1;

  /**
   * @name Helpers#isSelectedAllStrict
   * @inner
   *
   * @description
   * Strictly compare selected items ids and model list
   *
   * @param {Array} conversations
   * @param {Array} selected
   * @type {function}
   * @returns {boolean}
   */
  const isSelectedAllStrict = (conversations, selected) => {
    if (conversations.length !== selected.length) {
      return false;
    }

    const matches = conversations.filter(item => isSelected(item, selected));

    return matches.length === conversations.length;
  };

  /**
   * @name Helpers#isSelectedAll
   *
   * @description
   * Helper to know if ALL conversations are selected.
   *
   * @param {array} conversations
   * @param {Array} selected
   * @type {function}
   * @returns {boolean}
   */
  const isSelectedAll = (conversations, selected) =>
    conversations && conversations.length > 0 && isSelectedAllStrict(conversations, selected);

  /**
   * @name Helpers#isSelectedPart
   *
   * @description
   * Helper to know if at least one conversation is selected.
   *
   * @param {array} conversations
   * @param {Array} selected
   * @type {function}
   * @returns {boolean}
   */
  const isSelectedPart = (conversations, selected) =>
    selected.length > 0 && !isSelectedAll(conversations, selected);

  /**
   * @name Helpers#toggleSelection
   *
   * @description
   * Helper to select/deselect single conversation.
   *
   * @param {Object} conversation
   * @param {Array} selected
   * @param {Object} event DOM click event
   * @type {function}
   */
  const toggleSelection = (conversation, selected, event) => {
    event.stopPropagation();

    if (isSelected(conversation, selected)) {
      selected.splice(selected.findIndex(item => item === conversation), 1);
    } else {
      selected.push(conversation);
    }
  };

  /**
   * @name Helpers#toggleAllConversations
   *
   * @description
   * Helper to select/deselect all conversations loaded.
   *
   * @param {array} conversations
   * @param {Array} selected
   * @type {function}
   */
  const toggleAllConversations = (conversations, selected) => {
    if (isSelectedAll(conversations, selected)) {
      selected.splice(0, selected.length);
    } else {
      selected.push(...conversations.filter(item => !isSelected(item, selected)));
    }
  };

  /**
   * @name Helpers#confirmConversationRemove
   *
   * @description
   * Opens confirmation modal for remove conversation.
   *
   * @param {Object} conversation
   * @param {Array} selected
   * @param {Object} event DOM click event
   * @type {function}
   */
  const confirmConversationRemove = (conversation, ext, event) => {
    event.stopPropagation();
    Object.assign(ext, {
      removeConfirmOpened: true,
      conversationToRemove: conversation,
    });
  };

  return {
    headers,
    getTopicType,
    getTopicLabel,
    isSelected,
    isSelectedAll,
    isSelectedPart,
    toggleSelection,
    toggleAllConversations,
    confirmConversationRemove,
    messageFromError: (error) => ErrorMessage[error],
    statusMessage: (statusObject) => {
      if (statusObject.i18n) {
        return i18n(statusObject.i18n);
      }

      return statusObject.text || '';
    },
  };
};

export default helpers;



